import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { postResults, RootState, SubmitStatus } from './store/store';
import { NonIdealState, Spinner } from '@blueprintjs/core';

export const FinishedView = () => {

    const dispatch = useDispatch();
    const status = useSelector<RootState>(state => state.submitStatus);
    useEffect(() => {
        if (status === SubmitStatus.Idle) {
            dispatch(postResults());
        }
    }, [status, dispatch]);

    if (status === SubmitStatus.Idle) {
        return <NonIdealState
            icon={'box'}
            title={'Finishing Up'}
            description={'Preparing to submit results, please don\'t exit your browser window'}/>
    }
    else if (status === SubmitStatus.Pending) {
        return <NonIdealState
            icon={<Spinner/>}
            title={'Finishing Up'}
            description={'Submitting results, please don\'t exit your browser window.'}/>
    }
    else if (status === SubmitStatus.Success) {
        return <NonIdealState
            icon={'thumbs-up'}
            title={"You're Finished!"}
            description={"Thank you for participating in this user study. You may now exit your browser window."}/>;
    }
    else {
        return <NonIdealState
            icon={'error'}
            title={"Uh Oh!"}
            description={"Something isn't right..."}/>
    }

}

