import React from 'react';
import { Button, ButtonGroup, Menu, MenuItem, Navbar, Popover, Position } from '@blueprintjs/core';
import { SterlingConnection } from '../../connections/SterlingConnection';

export interface IGraphNavbarProps {
    connection: SterlingConnection
    canRequestContrastingCase: boolean
    title?: string
}

class GraphNavbar extends React.Component<IGraphNavbarProps> {

    render () {
        const props = this.props;
        return <Navbar className={'nav'} fixedToTop={false}>
            <Navbar.Group>
                {
                    props.title && <Navbar.Heading>{ props.title }</Navbar.Heading>
                }
                {/*<ButtonGroup style={{minWidth: 120}}>*/}
                {/*    <Popover*/}
                {/*        minimal={true}*/}
                {/*        position={Position.BOTTOM_LEFT}>*/}
                {/*        <Button rightIcon={'caret-down'}*/}
                {/*                fill={true}*/}
                {/*                text={'Layout Options'}/>*/}
                {/*        {this._layoutMenu()}*/}
                {/*    </Popover>*/}
                {/*</ButtonGroup>*/}
            </Navbar.Group>
            {
                this.props.canRequestContrastingCase && this._contrastingCasesButtons()
            }
        </Navbar>
    }

    private _contrastingCasesButtons = (): React.ReactNode => (
        <Navbar.Group>
            <Button outlined
                    intent={'success'}
                    onClick={() => this.props.connection.requestCompareCaseMin()}
                    text={'sat-min'}/>
            <Button outlined
                    intent={'success'}
                    onClick={() => this.props.connection.requestCompareCaseMax()}
                    text={'sat-max'}/>
            <Button outlined
                    intent={'success'}
                    onClick={() => this.props.connection.requestContrastCaseMin()}
                    text={'unsat-min'}/>
            <Button outlined
                    intent={'success'}
                    onClick={() => this.props.connection.requestContrastCaseMax()}
                    text={'unsat-max'}/>
        </Navbar.Group>
    )

    private _layoutMenu = (): React.ReactNode => (
        <Menu>
            <MenuItem text={'Test'} icon={'lab-test'}/>
        </Menu>
    )

}

export default GraphNavbar;