import React from 'react';
import { AlloyInstance, AlloySignature } from 'alloy-ts';
import { DagreLayout, Graph, Node, NodeStyle, RectangleStyle } from '@atdyer/graph-js';
import { generateGraph } from './graphData';
import { Map as IMap } from 'immutable';
import { NonIdealState, ResizeSensor } from '@blueprintjs/core';

export interface IGraphStageProps {
    instance: AlloyInstance | null,
    graph: Graph
}

const NODE_CACHE = new Map<string, Node>();
const PROJECTIONS = IMap<string, string>();
const ATTRIBUTES = IMap<string, boolean>();
const HIDEDISCONNECTED = IMap<string, boolean>().set('Int', true);
const DEFAULT_NODE_STYLES: NodeStyle[] = [{
    nodes: [],
    shape: { type: 'circle' }
}];

class GraphStage extends React.Component<IGraphStageProps> {

    private _ref: React.RefObject<HTMLCanvasElement>;

    constructor (props: IGraphStageProps) {

        super(props);
        this._ref = React.createRef<HTMLCanvasElement>();

    }

    componentDidMount () {

        const canvas = this._ref.current;
        const props = this.props;
        const instance = props.instance;
        const graph = props.graph;
        if (instance) this._update(graph, instance);
        if (canvas) graph.canvas(canvas);
        graph.update();
        graph.zoomToFit(0);

    }

    componentDidUpdate (prevProps: Readonly<IGraphStageProps>, prevState: Readonly<{}>, snapshot?: any) {
        const props = this.props;
        const instance = props.instance;
        const graph = props.graph;
        const canvas = this._ref.current;
        if (prevProps.instance !== instance && instance !== null) {
            this._update(graph, instance);
            if (canvas) graph.canvas(canvas);
            graph.update();
            graph.zoomToFit(0);
        }
    }

    render () {

        return this.props.instance
            ? <ResizeSensor onResize={this._onResize}>
                <canvas className={'graph'} ref={this._ref}/>
              </ResizeSensor>
            : <NonIdealState
                title={'No Instance'}
                icon={'graph'}/>;

    }

    private _onResize = () => {
        this.props.graph.resize();
    }

    private _update (graph: Graph, instance: AlloyInstance) {

        const [nodes, edges] = generateGraph(
            instance,
            graph.nodes(),
            NODE_CACHE,
            PROJECTIONS,
            ATTRIBUTES,
            HIDEDISCONNECTED
        );

        graph.nodes(nodes);
        graph.edges(edges);
        graph.axesVisible(false);
        graph.gridVisible(false);
        graph.nodeStyles(this._buildNodeStyles());

        const dagre = new DagreLayout();
        dagre.apply(graph, {
            nodesep: 100,
            rankdir: 'BT',
            ranksep: 150
        });

        const canvas = this._ref.current;
        if (canvas) this.props.graph.canvas(canvas);

    }

    private _buildNodeStyles (): NodeStyle[] {

        const instance = this.props.instance;
        if (!instance) return DEFAULT_NODE_STYLES;

        const univ = instance.univ();
        if (!univ) return DEFAULT_NODE_STYLES;

        const populate = (sig: AlloySignature): NodeStyle => {
            const children = sig.subTypes().map(populate);
            // const shape: CircleStyle = {
            //     type: 'circle',
            //     radius: 45
            // };
            const shape: RectangleStyle = {
                type: 'rectangle',
                width: 100,
                height: 45
            }
            return {
                nodes: sig.atoms().map(atom => atom.name()),
                shape,
                children
            }
        }

        const styletree = populate(univ);
        return [styletree];

    }

}

export default GraphStage;