import React from 'react';
import { AlloyInstance } from 'alloy-ts';
import { Graph } from '@atdyer/graph-js';
import GraphStage from '../features/graph/GraphStage';
import { Navbar, NavbarGroup, NavbarHeading } from '@blueprintjs/core';

export interface IGraphViewProps {
    instance: AlloyInstance
    graph: Graph
    class?: string
    message?: string
}

class GraphView extends React.Component<IGraphViewProps> {

    render (): React.ReactNode {
        const props = this.props;
        return (
            <div className={'graph-view ' + (props.class ? props.class : '')}>
                {
                    props.message &&
                    <Navbar className={'nav graph-nav'} fixedToTop={false}>
                        <NavbarGroup>
                            <NavbarHeading>
                                { props.message }
                            </NavbarHeading>
                        </NavbarGroup>
                    </Navbar>
                }
                <GraphStage
                    instance={props.instance}
                    graph={props.graph}/>
            </div>
        )
    }

}

export default GraphView;