import React from 'react';
import { RootState } from '../store/store';
import { setContrastingCase, setInstance } from '../store/sterling/sterlingSlice';
import { connect, ConnectedProps } from 'react-redux';
import SterlingNavbar from '../features/nav/SterlingNavbar';
import { AlloyInstance } from 'alloy-ts';
import GraphView from '../features/graph/GraphView';
import { Graph } from '@atdyer/graph-js';
import { SterlingConnection } from '../connections/SterlingConnection';

const mapState = (state: RootState) => ({
    ...state
});

const mapDispatch = {
    setInstance,
    setContrastingCase
};

const connector = connect(mapState, mapDispatch);

type SterlingConnectedProps = ConnectedProps<typeof connector>;

export type SterlingProps = SterlingConnectedProps & {
    connection: SterlingConnection
}

interface SterlingState {
    compareType: string
}

class Sterling extends React.Component<SterlingProps, SterlingState> {

    constructor (props: SterlingProps) {
        super(props);
        this.state = {
            compareType: ''
        };
    }

    componentDidMount () {

        this._initializeConnection();

    }

    render (): React.ReactNode {

        return <div className={'sterling'}>
            <SterlingNavbar connection={this.props.connection}/>
            { this._renderStage() }
        </div>

    }

    private _initializeConnection = (): void => {

        const connection = this.props.connection;

        connection.addEventListener('connect', () => {
            connection.requestCurrentInstance();
        });

        connection.addEventListener('instance', event => {
            this.props.setInstance(event.instance);
        });

        connection.addEventListener('compare-min', event => {
            this.props.setContrastingCase(event.instance);
            this.setState({ compareType: 'sat-min' });
        });

        connection.addEventListener('compare-max', event => {
            this.props.setContrastingCase(event.instance);
            this.setState({ compareType: 'sat-max' });
        });

        connection.addEventListener('contrast-min', event => {
            this.props.setContrastingCase(event.instance);
            this.setState({ compareType: 'unsat-min' });
        });

        connection.addEventListener('contrast-max', event => {
            this.props.setContrastingCase(event.instance);
            this.setState({ compareType: 'unsat-max' });
        });

        connection.connect();

    }

    private _renderStage = (): React.ReactNode => {

        const props = this.props;
        const instance = props.instance;
        const instanceGraph = props.instanceGraph;
        const contrast = props.contrast;
        const contrastGraph = props.contrastGraph;

        if (instance === null) {
            return Sterling._renderPlaceholder();
        } else {
            if (contrast === null) {
                return this._renderSingleInstance(instance, instanceGraph);
            } else {
                return this._renderInstanceWithContrast(instance, instanceGraph, contrast, contrastGraph);
            }
        }

    }

    private static _renderPlaceholder (): React.ReactNode {
        return <div className={'stage placeholder'}>Generate an instance.</div>
    }

    private _renderSingleInstance (instance: AlloyInstance, graph: Graph): React.ReactNode {
        return <div className={'stage'}>
            <GraphView
                canRequestContrastingCase={true}
                connection={this.props.connection}
                instance={instance}
                graph={graph}/>
        </div>;
    }

    private _renderInstanceWithContrast (
        instance: AlloyInstance,
        instanceGraph: Graph,
        contrast: AlloyInstance,
        contrastGraph: Graph): React.ReactNode {
        return <div className={'stage'}>
            <GraphView
                canRequestContrastingCase={true}
                connection={this.props.connection}
                instance={instance}
                graph={instanceGraph}/>
            <div className={'divider'}/>
            <GraphView
                title={this.state.compareType}
                canRequestContrastingCase={false}
                connection={this.props.connection}
                instance={contrast}
                graph={contrastGraph}/>
        </div>
    }

}

export default connector(Sterling);