import React from 'react';
import { Button, Navbar, NavbarDivider, NavbarHeading } from '@blueprintjs/core';
import { SterlingConnection } from '../../connections/SterlingConnection';

export interface ISterlingNavbarProps {
    connection: SterlingConnection
}

interface ISterlingNavbarState {
    connected: boolean
    ready: boolean
}

class SterlingNavbar extends React.Component<ISterlingNavbarProps, ISterlingNavbarState> {

    constructor(props: ISterlingNavbarProps) {
        super(props);
        this.state = {
            connected: false,
            ready: false
        }
    }

    componentDidMount() {

        const connection = this.props.connection;

        connection
            .addEventListener('connect', () => {
                this.setState({connected: true});
            })
            .addEventListener('disconnect', () => {
                this.setState({connected: false, ready: false});
            })
            .addEventListener('instance', event => {
                this.setState({
                    ready: this.state.connected
                });
            });

    }

    render() {

        const state = this.state;

        return (
            <Navbar fixedToTop className={'nav bp3-dark'}>
                <Navbar.Group>
                    <NavbarHeading className={'nav-heading'}>
                        Sterling <span className={'muted'}>Lite</span>
                    </NavbarHeading>
                    <NavbarDivider/>
                </Navbar.Group>
                <Navbar.Group>
                    <Button disabled={!state.ready}
                            intent={state.connected ? 'success' : 'danger'}
                            large={true}
                            onClick={this._requestNext}
                            rightIcon={'circle-arrow-right'}
                            text={'Next Instance'}/>
                </Navbar.Group>
            </Navbar>
        );

    }

    private _requestNext = (): void => {

        this.props.connection.requestNextInstance();

    }
}

export default SterlingNavbar;