import React from 'react';
import { Button, ButtonGroup, NonIdealState } from '@blueprintjs/core';
import GraphView from './GraphView';
import { AlloyInstance } from 'alloy-ts';
import { Graph } from '@atdyer/graph-js';


export interface EvalProps {
    instance: AlloyInstance
    graph: Graph
    onSubmit: (answer: boolean) => void
}

class EvalView extends React.Component<EvalProps> {

    render (): React.ReactNode {

        const props = this.props;
        const instance = props.instance;
        const graph = props.graph;

        return (
            <>
                <div className={'eval-question'}>
                    <NonIdealState
                        title={'Does this instance satisfy the spec?'}>
                        <ButtonGroup>
                            <Button large intent={'success'} text={'Yes'} onClick={this._answerYes}/>
                            <Button large intent={'danger'} text={'No'} onClick={this._answerNo}/>
                        </ButtonGroup>
                    </NonIdealState>
                </div>
                <div className={'divider'}/>
                <GraphView
                    class={'eval-graph'}
                    instance={instance}
                    graph={graph}/>
            </>
        );
    }

    private _answerYes = () => {
        this.props.onSubmit(true);
    }

    private _answerNo = () => {
        this.props.onSubmit(false);
    }

}

export default EvalView;