import { configureStore, createAsyncThunk, createSlice, getDefaultMiddleware, PayloadAction } from '@reduxjs/toolkit';
import * as studyData from '../data/study-0.json';
import moment, { Moment } from 'moment';

export interface ExploreSet {
    target: string
    sat: string
    unsat: string
}

export enum Page {
    Welcome = 'welcome',
    Explore = 'explore',
    Evaluate = 'evaluate',
    Retake = 'retake',
    Finished = 'finished'
}

export enum SubmitStatus {
    Idle = 'idle',
    Pending = 'pending',
    Success = 'success',
    Fail = 'fail'
}

export interface UserStudyState {
    exploreSet: ExploreSet[]
    evaluateSet: string[]
    solution: boolean[]
    page: Page
    current: number
    evalQuestion: number
    evalAttempt: number
    answers0: boolean[]
    answers1: boolean[]
    minCorrect: number
    submitStatus: SubmitStatus
}

type TrimmedUserStudyState = Omit<UserStudyState, "exploreSet" | "evaluateSet" | "solution">

interface LogItem {
    action: any,
    state: TrimmedUserStudyState
    seconds_since_start: number
    seconds_since_last: number
}

interface ResultsPackage {
    initialState: UserStudyState
    actions: LogItem[]
}

const ACTION_LOG: LogItem[] = [];
const FIRST_MOMENT: Moment = moment()
let LAST_MOMENT: Moment = FIRST_MOMENT;

// @ts-ignore
const log = store => next => action => {
    const result = next(action);
    const state = store.getState();
    const time = moment()
    ACTION_LOG.push({
        action,
        state: trimState(state),
        seconds_since_start: time.diff(FIRST_MOMENT, 'seconds'),
        seconds_since_last: time.diff(LAST_MOMENT, 'seconds')
    });
    LAST_MOMENT = time;
    return result;
}

function trimState (state: UserStudyState): TrimmedUserStudyState {
    return {
        page: state.page,
        current: state.current,
        evalQuestion: state.evalQuestion,
        evalAttempt: state.evalAttempt,
        answers0: state.answers0,
        answers1: state.answers1,
        minCorrect: state.minCorrect,
        submitStatus: state.submitStatus
    };
}

const initialState: UserStudyState = {
    exploreSet: studyData.instances,
    evaluateSet: studyData.evaluation,
    solution: studyData.solution,
    page: Page.Welcome,
    current: 0,
    evalQuestion: 0,
    evalAttempt: 0,
    answers0: [],
    answers1: [],
    minCorrect: studyData.minCorrect,
    submitStatus: SubmitStatus.Idle
}

export const postResults = createAsyncThunk('submit/results', async (_, thunkAPI) => {
    const data = {
        initialState,
        actions: ACTION_LOG
    };
    const response = await fetch('/survey', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
    });

    if (response.ok) {
        const result = await response.text();
        if (result === 'success') return true;
    }

    return thunkAPI.rejectWithValue(false);
});

const userStudySlice = createSlice({
    name: 'user-study',
    initialState: initialState,
    reducers: {
        beginStudy (state) {
            state.page = Page.Explore;
            state.evalAttempt = 0;
        },
        beginRetake (state) {
            state.page = Page.Explore;
            state.evalAttempt = 1;
            state.current = 0;
        },
        beginEvaluation (state) {
            state.page = Page.Evaluate;
            state.evalQuestion = 0;
        },
        nextInstanceSet (state) {
            const cur = state.current;
            const len = state.exploreSet.length;
            if (len && cur < len-1) {
                state.current += 1;
            }
        },
        previousInstanceSet(state) {
            const cur = state.current;
            const len = state.exploreSet.length;
            if (len && cur > 0) {
                state.current -= 1;
            }
        },
        reset (state) {
            state.page = Page.Welcome;
        },
        submitEvalAnswer (state, action: PayloadAction<boolean>) {

            const attempt = state.evalAttempt;
            const question = state.evalQuestion;

            // Save the answer
            if (attempt === 0) {

                state.answers0[question] = action.payload;

            } else if (attempt === 1) {

                state.answers1[question] = action.payload;

            }

            // Either go on to the next question or calculate score
            // if this was the last question
            if (question < state.evaluateSet.length - 1) {

                // Go on to the next question
                state.evalQuestion += 1

            } else {

                // If this is the first attempt, calculate score and redirect
                // to the appropriate page
                if (attempt === 0) {

                    let score = 0;
                    state.answers0.forEach((answer: boolean, i: number) => {
                        if (answer === state.solution[i]) score += 1;
                    });

                    // Not good enough score, send back
                    if (score < state.minCorrect) {
                        state.page = Page.Retake
                    }

                    // Score good enough, finish up
                    else {
                        state.page = Page.Finished
                    }

                }

                // If this is the second attempt, calculate score and finish
                else {
                    state.page = Page.Finished;
                }

            }
        }
    },
    extraReducers: builder => {
        builder.addCase(postResults.pending, state => {
            state.submitStatus = SubmitStatus.Pending;
        });
        builder.addCase(postResults.fulfilled, state => {
            state.submitStatus = SubmitStatus.Success;
        });
        builder.addCase(postResults.rejected, state => {
            state.submitStatus = SubmitStatus.Fail;
        });
    }
});

export type RootState = ReturnType<typeof userStudySlice.reducer>;
export const store = configureStore({
    reducer: userStudySlice.reducer,
    middleware: getDefaultMiddleware().concat(log),
    devTools: process.env.NODE_ENV !== 'production'
});

export const {
    beginStudy,
    beginRetake,
    beginEvaluation,
    nextInstanceSet,
    previousInstanceSet,
    reset,
    submitEvalAnswer
} = userStudySlice.actions;