import React from 'react';
import UserStudyNavbar from './UserStudyNavbar';
import { beginRetake, beginStudy, ExploreSet, Page, reset, RootState, submitEvalAnswer } from './store/store';
import { connect, ConnectedProps } from 'react-redux';
import GraphView from './GraphView';
import { Button, NonIdealState } from '@blueprintjs/core';
import EvalView from './EvalView';
import { AlloyInstance } from 'alloy-ts';
import { Graph } from '@atdyer/graph-js';
import { FinishedView } from './FinishedView';

export interface Instance {
    instance: AlloyInstance
    graph: Graph
}

export interface InstanceSet {
    target: Instance
    sat: Instance
    unsat: Instance
}

const mapState = (state: RootState) => ({ ...state });
const mapDispatch = { beginStudy, beginRetake, reset, submitEvalAnswer };
const connector = connect(mapState, mapDispatch);
type UserStudyProps = ConnectedProps<typeof connector>;

class UserStudy extends React.Component<UserStudyProps> {

    private readonly _instanceSet: InstanceSet[];
    private readonly _evaluateSet: Instance[];

    constructor (props: UserStudyProps) {

        super(props);

        this._instanceSet = props.exploreSet.map(generateInstanceSet);
        this._evaluateSet = props.evaluateSet.map(generateInstance);

    }

    render (): React.ReactNode {

        return (
            <div className={'sterling'}>
                <UserStudyNavbar/>
                <div className={'stage'}>
                    {
                        this._renderStage()
                    }
                </div>
            </div>
        );

    }

    private _renderStage (): React.ReactNode {
        switch (this.props.page) {
            case Page.Welcome:
                return this._renderWelcome();
            case Page.Explore:
                return this._renderGraphs();
            case Page.Evaluate:
                return this._renderEval();
            case Page.Retake:
                return this._renderRetake();
            case Page.Finished:
                return this._renderFinished();
            default:
                return this._renderPlaceholder();
        }
    }

    private _renderWelcome (): React.ReactNode {
        return (
            <NonIdealState
                title={"Sterling User Study"}
                description={"A description of the user study can be placed here."}
                icon={"predictive-analysis"}
                action={
                    <Button
                        large
                        intent={'success'}
                        text={"Begin"}
                        onClick={() => this.props.beginStudy()}/>
                }/>
        );
    }

    private _renderGraphs (): React.ReactNode {
        const props = this.props;
        const instanceset = this._instanceSet[props.current];
        return (
            <>
                <GraphView
                    class={'instance'}
                    message={'This instance satisfies the specification'}
                    instance={instanceset.target.instance}
                    graph={instanceset.target.graph}/>
                <div className={'divider'}/>
                <GraphView
                    class={'sat-instance'}
                    message={'This instance satisfies the specification'}
                    instance={instanceset.sat.instance}
                    graph={instanceset.sat.graph}/>
                <div className={'divider'}/>
                <GraphView
                    class={'unsat-instance'}
                    message={'This instance does NOT satisfy the specification'}
                    instance={instanceset.unsat.instance}
                    graph={instanceset.unsat.graph}/>
            </>
        );
    }

    private _renderEval (): React.ReactNode {

        const props = this.props;
        const question = props.evalQuestion;
        const instance = this._evaluateSet[question].instance;
        const graph = this._evaluateSet[question].graph;
        return (
            <EvalView
                instance={instance}
                graph={graph}
                onSubmit={props.submitEvalAnswer}/>);

    }

    private _renderRetake (): React.ReactNode {
        return (
            <NonIdealState
                icon={'refresh'}
                title={'Please Revisit the Model'}
                description={'You didn\'t get enough questions correct. You now have an opportunity to revisit the model and instances before attempting the evaluation once more.'}>
                <Button
                    large
                    intent={'success'}
                    text={'I\'m Ready!'}
                    onClick={() => this.props.beginRetake()}/>
            </NonIdealState>
        )
    }

    private _renderFinished (): React.ReactNode {

        return <FinishedView/>;

    }

    private _renderPlaceholder (): React.ReactNode {
        return (
            <NonIdealState
                title={"Sterling User Study"}
                description={"Uh-oh, Tristan screwed up. You shouldn't be here..."}
                icon={"error"}
                action={
                    <Button
                        text={"Try Resetting"}
                        onClick={() => this.props.reset()}/>
                }/>
        );
    }

}

function generateInstance (instance: string): Instance {
    return {
        instance: new AlloyInstance(instance),
        graph: new Graph()
    };
}

function generateInstanceSet (exploreset: ExploreSet): InstanceSet {
    return {
        target: {
            instance: new AlloyInstance(exploreset.target),
            graph: new Graph()
        },
        sat: {
            instance: new AlloyInstance(exploreset.sat),
            graph: new Graph()
        },
        unsat: {
            instance: new AlloyInstance(exploreset.unsat),
            graph: new Graph()
        }
    };
}

export default connector(UserStudy);