import React from 'react';
import { Button, ButtonGroup, Navbar, NavbarDivider, NavbarGroup, NavbarHeading } from '@blueprintjs/core';
import { RootState, nextInstanceSet, previousInstanceSet, beginEvaluation, Page } from './store/store';
import { connect, ConnectedProps } from 'react-redux';

const mapState = (state: RootState) => ({ ...state });
const mapDispatch = { nextInstanceSet, previousInstanceSet, beginEvaluation };
const connector = connect(mapState, mapDispatch);
type UserStudyNavbarProps = ConnectedProps<typeof connector>;

class UserStudyNavbar extends React.Component<UserStudyNavbarProps> {

    render (): React.ReactNode {
        return (
            <Navbar fixedToTop className={'nav bp3-dark'}>
                <NavbarGroup>
                    <NavbarHeading className={'nav-heading'}>
                        Sterling <span className={'muted'}>Lite</span>
                    </NavbarHeading>
                    <NavbarDivider/>
                </NavbarGroup>
                {
                    this._renderRightSide()
                }
            </Navbar>
        )
    }

    private _renderRightSide = () => {
        switch (this.props.page) {
            case Page.Explore: return this._renderExplore()
            case Page.Evaluate: return this._renderEvaluate()
            default: return null;
        }
    }

    private _renderExplore = () => {
        return <NavbarGroup>
            <NavbarHeading>
                { (this.props.current + 1) + '/' + this.props.exploreSet.length }
            </NavbarHeading>
            <ButtonGroup>
                <Button
                    intent={'primary'}
                    large={true}
                    icon={'circle-arrow-left'}
                    text={'Previous'}
                    onClick={() => this.props.previousInstanceSet()}/>
                <Button
                    intent={'primary'}
                    large={true}
                    rightIcon={'circle-arrow-right'}
                    text={'Next'}
                    onClick={() => this.props.nextInstanceSet()}/>
            </ButtonGroup>
            <NavbarDivider/>
            <ButtonGroup>
                <Button
                    intent={'primary'}
                    large={true}
                    text={'Begin Evaluation'}
                    onClick={() => this.props.beginEvaluation()}/>
            </ButtonGroup>
        </NavbarGroup>;
    }

    private _renderEvaluate = () => {
        return <NavbarGroup>
            <NavbarHeading>
                { 'Question ' + (this.props.evalQuestion + 1) + '/' + this.props.evaluateSet.length }
            </NavbarHeading>
        </NavbarGroup>
    }

}

export default connector(UserStudyNavbar);