import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import * as serviceWorker from './serviceWorker';
import { ForgeConnection } from './connections/ForgeConnection';
import { store } from './store/store';
import { store as userStudyStore } from './user-study/store/store';
import Sterling from './sterling/Sterling';
import './styles/index.scss';
import UserStudy from './user-study/UserStudy';

const target = process.env.REACT_APP_BUILD_TARGET;

if (target === 'user-study') {
    ReactDOM.render(
        <React.StrictMode>
            <Provider store={userStudyStore}>
                <UserStudy/>
            </Provider>
        </React.StrictMode>,
        document.getElementById('root')
    )
} else {
    const connection = new ForgeConnection();
    ReactDOM.render(
        <React.StrictMode>
            <Provider store={store}>
                <Sterling connection={connection}/>
            </Provider>
        </React.StrictMode>,
        document.getElementById('root')
    );
}


// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
