import {AlloyInstance} from "alloy-ts";
import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import { Graph } from '@atdyer/graph-js';

export interface SterlingState {
    instance: AlloyInstance | null,
    contrast: AlloyInstance | null,
    instanceGraph: Graph,
    contrastGraph: Graph
}

const initialState: SterlingState = {
    instance: null,
    contrast: null,
    instanceGraph: new Graph(),
    contrastGraph: new Graph()
}

const sterlingSlice = createSlice({
    name: 'sterling',
    initialState: initialState,
    reducers: {
        setInstance (state, action: PayloadAction<AlloyInstance | null>) {
            state.instance = action.payload;
            state.contrast = null;
        },
        setContrastingCase (state, action: PayloadAction<AlloyInstance | null>) {
            state.contrast = action.payload;
        }
    }
});

export const {
    setInstance,
    setContrastingCase
} = sterlingSlice.actions;
export default sterlingSlice.reducer;
