import React from 'react';
import { AlloyField, AlloyInstance, AlloySignature } from 'alloy-ts';
import GraphNavbar from './GraphNavbar';
import { HTMLTable } from '@blueprintjs/core';
import { Graph } from '@atdyer/graph-js';
import GraphStage from './GraphStage';
import { SterlingConnection } from '../../connections/SterlingConnection';

export interface IGraphViewProps {
    connection: SterlingConnection
    instance: AlloyInstance
    graph: Graph
    canRequestContrastingCase: boolean
    title?: string
}

class GraphView extends React.Component<IGraphViewProps> {

    render () {

        const props = this.props;

        return (
            <div className={'graph-view'}>
                <GraphNavbar
                    connection={props.connection}
                    canRequestContrastingCase={props.canRequestContrastingCase}
                    title={props.title}/>
                <GraphStage instance={props.instance} graph={props.graph}/>
            </div>
        )

        // return (
        //     <div className={'graph-view'}>
        //         <GraphNavbar
        //             connection={this.props.connection}
        //             canRequestContrastingCase={this.props.canRequestContrastingCase}/>
        //         <div className={'temp-tables'}>
        //             {
        //                 this.props.instance.signatures()
        //                     .filter(sig => sig.atoms().length)
        //                     .sort((a, b) => a.atoms().length - b.atoms().length)
        //                     .map(GraphView._signatureTable)
        //             }
        //             {
        //                 this.props.instance.fields()
        //                     .map(GraphView._fieldTable)
        //             }
        //         </div>
        //     </div>
        // );
    }

    private static _signatureTable = (signature: AlloySignature, index: number): React.ReactNode => (
        <HTMLTable key={index}
                   className={'bp3-html-table-bordered bp3-html-table-condensed bp3-html-table-striped'}>
            <thead>
            <tr>
                <th>{signature.name()}</th>
            </tr>
            </thead>
            <tbody>
            {
                signature.atoms().map((atom, i) => (
                    <tr key={i}>
                        <td>{atom.name()}</td>
                    </tr>
                ))
            }
            </tbody>
        </HTMLTable>
    )

    private static _fieldTable = (field: AlloyField, index: number): React.ReactNode => (
        <div className={'table-container'} key={index}>
            {field.name()}
            <HTMLTable className={'bp3-html-table-bordered bp3-html-table-condensed bp3-html-table-striped'}>
                <thead>
                <tr>
                {
                    field.types().map((sig, i) => (
                        <th key={i}>{sig.name()}</th>
                    ))
                }
                </tr>
                </thead>
                <tbody>
                {
                    field.tuples().map((tuple, i) => (
                        <tr key={i}>{
                            tuple.atoms().map((atom, j) => (
                                <td key={j}>{atom.name()}</td>
                            ))
                        }</tr>
                    ))
                }
                </tbody>
            </HTMLTable>
        </div>
    )

}

export default GraphView;